<template class="container">
    <div v-if="cartList.length" >
        <table class="table table-hover">
            <thead>
                <tr>
                    <th class="th-wide">商品信息</th>
                    <th class="th-thin">商品单价</th>
                    <th class="th-wide">商品数量</th>
                    <th class="th-thin">小计</th>
                    <th class="th-thin">操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in cartList">
                    <td>
                        <img :src="productDictList[item.id].image">
                        <router-link :to="'/productDetail/' + item.id">
                            {{ productDictList[item.id].name }}
                        </router-link>
                    </td>
                    <td class="price">
                        ¥ {{ productDictList[item.id].cost }}
                    </td>
                    <td>
                        <button class="btn btn-default" @click="handleCount(index, -1)" :disabled="item.count === 1">-</button>
                        <input type="text" class="btn" disabled v-model="item.count">
                        <button class="btn btn-default" @click="handleCount(index, 1)">+</button>
                    </td>
                    <td class="price">
                        ¥ {{ productDictList[item.id].cost * item.count }}
                    </td>
                    <td>
                        <button  class="btn btn-primary" @click="handleDelete(index)">移除</button>
                    </td>
                </tr>
            </tbody>
        </table>
        <div class="cart-footer">
            <div class="promo">
                <span>使用优惠券</span>
                <input type="text" v-model="promotionCode" placeholder="输入tadm可享受惊喜优惠">
                <button class="btn btn-success" @click="handleCheckCode">验证</button>
                <span class="price" v-if="promotion">
                    (优惠  ¥ {{ promotion }})
                </span>
            </div>
            <div class="cart-footer-info">
                共计 <span class="count">{{ countAll }}</span> 件
            </div>
            <div class="cart-footer-info">
                应付总金额(RMB)
                <span class="price">¥ {{ costAll - promotion }}</span>
            </div>
            <div class="cart-footer-info">
                <button class="btn btn-success" @click="handleOrder">马上支付</button>
            </div>
        </div>
    </div>
    <div class="empty" v-else>当前购物车暂无商品,快去加入商品吧！</div>
</template>

<script>
    import product_data from '../product';
    export default {
        name: "cart",
        data(){
            return {
                promotion: 0,
                promotionCode: '',
                productList: product_data
            }
        },
        computed: {
            cartList(){
                return this.$store.state.cartList
            },
            productDictList(){
                const dict = {}
                this.productList.forEach(
                    item => dict[item.id] = item
                )
                return dict
            },
            countAll(){
                let count = 0
                this.cartList.forEach(
                    item => count += item.count
                )
                return count
            },
            costAll(){
                let cost = 0;
                this.cartList.forEach(
                    item => cost += this.productDictList[item.id].cost * item.count
                )
                return cost
            }
        },
        methods: {
            handleOrder(){
                this.$store.dispatch('buy').then(
                    () => window.alert('购买成功')
                )
                window.sessionStorage.setItem('cartList',[])
            },
            handleCheckCode(){
                if(this.promotionCode === ''){
                    window.alert('请输入优惠码')
                    return;
                }
                if(this.promotionCode !== 'tadm'){
                    window.alert('优惠码输入错误')
                    return;
                }
                this.promotion = 666;
            },
            handleCount(index, count){
                if(count < 0 && this.cartList[index].count === 1) return;
                this.$store.commit('editCartCount',{
                    id: this.cartList[index].id,
                    count: count
                });
            },
            handleDelete(index){
                this.$store.commit('deleteCart', this.cartList[index].id)
                window.sessionStorage.setItem('cartList',JSON.stringify(this.cartList))
            }
        },
        mounted(){
            this.$store.dispatch('getCartList')//提交异步,解决异步问题
        }
    }
</script>

<style scoped>
    table{
        margin: 0 5px 0 5px;
    }
    th{
        text-align: center;
    }
    .th-wide{
        width: 30%;
    }
    .th-thin{
        width: 13%;
    }
    img{
        width: 40px;
        height: 40px;
    }
    .price{
        color: red;
        padding-top: 18px;
    }
    .promo{
        float: left;
        padding-left: 30px;
        margin-right: 20px;
    }
    .count{
        color: red;
    }
    .cart-footer{
        padding: 2% 10% 0 10%;
    }
    .cart-footer-info{
        float: left;
        padding: 0 16px;
        font-size: 18px;
    }
    .empty{
        text-align: center;
        font-size: 18px;
        margin-top: 20px;
    }
    @media (max-width: 960px){
        img{
            width:100%;
            height: 60px;
        }
        .promo{
            position: absolute;
            left: 10px;
            padding-top: 20px;
        }
        input{
            width: 80px;
        }
        .cart-footer-info{
            position: relative;
            top: 66px;
        }
    }
</style>


