<template>
    <div class="signupbg">
        <template v-if="login">
            <div>
                <form class="login-info-form">
                <p>账户登录</p><br>
                <div class="form-group col-md-12">
                    <input placeholder="用户名/手机号" class="form-control" type="text" v-model="username" maxlength="20"/>
                </div>
                <div class="form-group col-md-12">
                    <input placeholder="输入密码" class="form-control" type="password" v-model="password" maxlength="15"/>
                </div>
                <div>
                    <button type="submit" @click="toShop" class="btn btn-primary">登录</button>
                    <button type="button" @click="changeLogin(false,true)" class="btn btn-primary">去注册</button>
                </div>
                <div class="footer">© 2019 Tadm.com 版权所有</div>
            </form>
            </div>
        </template>
        <template v-if="register">
            <form class="login-info-form">
                <p>欢迎注册</p><br>
                <div class="form-group col-md-12">
                    <input placeholder="用户名/手机号" class="form-control" type="text" v-model="username" maxlength="20"/>
                </div>
                <div class="form-group col-md-12">
                    <input placeholder="输入密码" class="form-control" type="password" v-model="password" maxlength="15"/>
                </div>
                <div class="form-group col-md-12">
                    <input placeholder="确认密码" class="form-control" type="password" v-model="confirmPassword" maxlength="15"/>
                </div>
                <div>
                    <button type="submit" @click="Register" class="btn btn-primary">注册</button>
                    <button type="button" @click="changeLogin(true,false)" class="btn btn-primary">已有账号</button>
                </div>
                <div class="footer">© 2019 Tadm.com 版权所有</div>
            </form>
        </template>
    </div>
</template>

<script>
    export default {
        name: "login",
        data(){
            return {
                login: false,
                register: true,
                username: '',
                password: '',
                confirmPassword: '',
            }
        },
        methods: {
            Register(){
                if( !this.username || !this.password ){
                    window.alert('账号或密码不能为空');
                    return;
                }
                if(this.confirmPassword !== this.password){
                    window.alert('密码不一致，请重新输入');
                    this.password = '';
                    this.confirmPassword = '';
                }
                else{
                    if(this.username!== window.sessionStorage.getItem('username') ){
                        this.$store.state.cartList=[]
                    }
                    window.sessionStorage.setItem('username', this.username);
                    window.sessionStorage.setItem('password', this.password);
                    this.register = false;
                    window.sessionStorage.setItem('loginStatus', 'login');
                    sessionStorage.setItem('cartList',JSON.stringify(this.$store.state.cartList))
                    this.$store.commit('getUser', this.username);
                    window.alert('注册成功，进入网站首页');
                    this.$store.state.loginStatus=window.sessionStorage.getItem('loginStatus');
                    this.$router.push('/list');
                }
            },
            changeLogin(l, r){
                this.login = l;
                this.register = r;
            },
            toShop(){
                let username = window.sessionStorage.getItem('username');
                let password = window.sessionStorage.getItem('password');
                if( !this.username || !this.password){
                    window.alert('账号或密码不能为空');
                    return;
                }
                if(username === this.username && password === this.password){
                    this.login = false;
                    window.sessionStorage.setItem('loginStatus', 'login');
                    this.$store.commit('getUser', this.username);
                    window.alert('登录成功，进入网站首页');
                    this.$store.state.loginStatus=window.sessionStorage.getItem('loginStatus');
                    this.$router.push('/list');
                }
                else{
                    window.alert('账号或密码错误');
                }
            },
        },
        created(){
            if(this.$route.params.loginStatus === 'logout'){
                this.$store.commit('getLoginStatus', false);
                return;
            }
        }
    }
</script>

<style scoped>
    .signupbg{
        background-image: url("../assets/signupbg.jpg");
        background-size: 100% 100%;
        background-repeat:repeat-x;
    }
    .login-info-form{
        width: 350px;
        height: 350px;
        background-color: #E9E9F2;
        position: relative;
        margin-left: 40%;
    }
    @media (max-width: 960px){
        .login-info-form{
            width: 250px;
            margin:0 auto;
        }
        button{
            width: 30px;
            margin: 5px;
        }
        .footer{
            font: 8px;
            position: absolute;
            bottom: 0%;
        }
    }
    p{
        font-size: 16px;
        color: #3c3c3c;
        font-weight: 700;
        text-align: center;
        padding-top: 20px;
     }
    button{
        width: 80px;
        margin: 20px;
    }
    .footer{
        color: #ccc3c3;
        font: 13px/1.5 "Microsoft YaHei",tahoma,arial,'Hiragino Sans GB','\5b8b\4f53',sans-serif;
        position: absolute;
        bottom: 0px;
  }
</style>


