import Vue from 'vue'
import Vuex from 'vuex'
import product_data from '../product'

Vue.use(Vuex)

// dispatch：异步操作 写法： this.$store.dispatch('mutations方法名',值)
// commit：同步操作 写法：this.$store.commit('mutations方法名',值)

const store = new Vuex.Store({
    state: {
        productList: [],
        cartList: [],
        cartList: JSON.parse(sessionStorage.getItem('cartList')),
        //使得在刷新操作时购物车数据不会丢失
        username: window.sessionStorage.getItem('username'),
        loginStatus: window.sessionStorage.getItem('loginStatus'),
    },
    getters: {
        brands: state => {
            //map返回一个新数组，原数组不会发生变化且适用于数组内所有元素使用同一种运算
            //Map转为Array使用扩展运算符... Set用于当前数组去重自动过滤
            const brands =[...new Set(state.productList.map(item => item.brand))]
            return brands;
        },
        colors: state => {
            const colors = [...new Set(state.productList.map(item => item.color))]
            return colors;
        }
    },
    mutations: { //同步
        setProductList(state, payload){
            state.productList = payload
        },
        addCart(state, id){
            //find 返回符合测试条件的第一个数组元素的值
            const add = state.cartList.find(
                item => item.id === id)
            //检测加入购物车的商品是否已经在列表中 防止购物车商品重复 此时加入便直接在指定ID上更改count
            if(add){
                add.count++
            }
            else{
                state.cartList.push({
                    id: id,
                    count: 1
                })
            }
            window.sessionStorage.setItem('cartList',JSON.stringify(state.cartList))
        },
        //edit cart product count
        // mutation改变的是state中的值，所以我们参数中有一个state方便我们快速改变对应的值
        // payload接收commit处传递过来的数据
        editCartCount(state, payload){
            const product = state.cartList.find(
                item => item.id === payload.id)
            product.count += payload.count
        },
        //delete cart selected id product
        deleteCart(state, id){
            const index = state.cartList.findIndex(
                item => item.id === id)
            state.cartList.splice(index, 1)
            // window.sessionStorage.removeItem('cartList'.splice(index, 1))
        },
        //clear cart
        emptyCart(state){
            state.cartList = [];
        },
        // getCartList(state,payload){
        //     state.cartList = payload
        // },
        getUser(state, username){
            state.username = username;
        },
        getLoginStatus(state, flag){
            state.loginStatus = flag;
        }
    },
    actions: {
        //异步请求product list，use setTimeout
        //context相当于store对象
        getProductList(context){
            setTimeout(() => {
                context.commit('setProductList', product_data)
            }, 500);
        },
        buy(context){
            //After use ajax ask for server do,then clear cart
            return new Promise(resolve => {
                setTimeout(() => {
                    context.commit('emptyCart');
                    resolve();
                }, 500);
            });
        },
    }
  })

  export default store


